@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;600&display=swap);
* {
    font-family: 'Indie Flower', cursive;
    background-color:rgb(90, 2, 102);
}


button {
  width: 120px;
  height: 40px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.19);
  border-radius: 8px;
  margin-right: 10px;
  background-color: rgb(8, 214, 241);
  border: 1px solid black;
  font-size: 30px;
  outline: none;
}
button:hover {
  width: 125px;
  height: auto;
  background-color: rgb(240, 225, 9);
  cursor: pointer;
}
h1 {
  color: blanchedalmond;
  font-size: 50px;
}
@media screen and (max-width: 1280px) { div.App {width: 1200px;} }
@media screen and (max-width: 1140px) { div.App {width: 1024px;} }
@media screen and (max-width: 992px) { div.App {width: 970px;} }
.App {
    text-align: center;
    border: rosybrown;
    padding-top: 5%;
    
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      -webkit-animation: App-logo-spin infinite 20s linear;
              animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @-webkit-keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  @keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
.developer {
padding-top: 300px;
padding-right: 1450px;
}
  
